<template>
	<div class="zhangdanList">
		<mt-loadmore :top-method="loadTop" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore" :auto-fill="false">
			<div v-for="(item, index) in msg" @click="into(index)" :key="item.id">
				<mt-cell :title="item.pname" is-link>
					<span>手动记账</span>
					<img slot="icon" :src="item.picUrl" alt="">
				</mt-cell>
			</div>
		</mt-loadmore>
	</div>
</template>
<script>
import { getFlowPlatformInfo } from './../api/api'
import { getStore, setStore } from '../config/mUtils'
export default {
	data() {
		return {
			allLoaded: false,
			searchCondition: { // 查询全部参数
				flag: 'all',
				current: 1 // 分页
			},
			msg: []
		}
	},
	methods: {
		loadTop() {
	  		this.init()
	  		this.$refs.loadmore.onTopLoaded();
		},
		loadBottom() {
			this.more()
		},
		more() {
			this.searchCondition.current = parseInt(this.searchCondition.current) + 1;
			let params = this.searchCondition;
			getFlowPlatformInfo(params).then(res => {
				if (res.code === 200) {
					// this.msg = res.data;
					for (var i = res.data.length - 1; i >= 0; i--) {
						this.msg.push(res.data[i]);
					}
					if (res.data.length < 14) {
						this.allLoaded = true
					} else {
						this.allLoaded = false
					}
					this.$refs.loadmore.onBottomLoaded()
					// this.$nextTick(() => {
					// 	this.scrollMode = 'touch';
					// })
				}
			})
		},
		init() { // 获取数据
			let params = {
				flag: 'all',
				current: 1 
			}
  			getFlowPlatformInfo(params).then(res => {
  				if (res.code === 200) {	
  					this.msg = res.data
  					this.allLoaded = false
  					this.searchCondition.current = 1
  				}
  			})
  		},
  		into(index) {
  			setStore('id', this.msg[index].id)
			setStore('pcode', this.msg[index].pcode)
			var userId = getStore('userId')
			if (userId !== null) {
				this.$router.push({path: '/jizhangDetail'})
			} else {
				this.$router.push({path: '/login', query: {detail: 3}})
			}
  		}
	},
	mounted() {
  		this.init()
	}
}
</script>
<style lang="scss" rel="stylesheet/scss">
	.zhangdanList {
		background: #f2f3f2;
		padding: 0.3rem 0.3rem 0.55rem 0.3rem; 
		.mint-cell {
			border-radius: 0.1rem;
			margin-bottom: 0.2rem;
			img {
				width: 0.5rem;
			}
		}
	}
</style>



